<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect
        :select-type="selectType"
        :only-content="onlyContent"
        :ratings="ratings"
        @ratingTypeChange="ratingTypeChange"
        @toggleContent="toggleContent"
      ></ratingselect>
      <div class="rating-wrapper">
        <ul>
          <li v-show="needShow(rating)" v-for="rating in ratings" :key="rating.rateTime" class="rating-item border-1px">
            <div class="avatar">
              <img width="28" height="28" :src="rating.avatar"/>
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <star :size="24" :score="rating.score"></star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" v-for="item in rating.recommend" :key="item">{{item}}</span>
              </div>
              <div class="time">
                {{formatRateTime(rating.rateTime)}}
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from '@better-scroll/core'
import star from '../components/common/star'
import split from '../components/common/split'
import ratingselect from '../components/common/ratingselect'
import { formatDate } from '../assets/js/date'

const POSITIVE = 0; // 正面评价
const NEGATIVE = 1; // 负面评价
const ALL = 2; // 所有

export default {
  name: 'ratings',
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    star,
    split,
    ratingselect
  },
  data() {
    return {
      ratings: [],
      selectType: ALL,
      onlyContent: true
    }
  },
  created() {
    this.axios.get('/api/ratings').then((response) => {
      var res = response.data;
      if (res.code == 0) {
        this.ratings = res.data;
        this.$nextTick(() => {
          if (!this.scroll) {
            if (this.$refs.ratings) { // 防止评论页刷新后，跳转到商品页后执行这里逻辑报错
              this.scroll = new BScroll(this.$refs.ratings, {
                click: true
              });
            }
          }
        });
      }
    })
  },
  computed: {

  },
  watch: {

  },
  mounted() {

  },
  methods: {
    ratingTypeChange(type) {
      this.selectType = type;
      this.$nextTick(() => {
        this.scroll.refresh();
      })
    },
    toggleContent() {
      this.onlyContent = !this.onlyContent;
      this.$nextTick(() => {
        this.scroll.refresh();
      })
    },
    needShow(rating) {
      if (this.onlyContent) {
        if (this.selectType == ALL) {
          return rating.text;
        }
        return rating.rateType == this.selectType && rating.text;
      } else {
        if (this.selectType == ALL) {
          return true;
        }
        return rating.rateType == this.selectType;
      }
    },
    formatRateTime(rateTime) {
      var time = new Date(rateTime);
      return formatDate(time, 'yyyy-MM-dd hh-mm');
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../assets/scss/mixins";

  .ratings{
    position: absolute;
    top: 174px;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
  }

  .overview{
    display: flex;
    padding: 18px 0;
    .overview-left{
      flex: 0 0 137px;
      padding: 6px 0;
      width: 137px;
      border-right: 1px solid rgba(7, 17, 27, 0.1);
      text-align: center;
      @media only screen and (max-width: 320px){
        flex: 0 0 120px;
        width: 120px;
      }
      .score{
        margin-bottom: 6px;
        line-height: 28px;
        font-size: 24px;
        color: rgb(255, 153, 0);
      }
      .title{
        margin-bottom: 8px;
        line-height: 12px;
        font-size: 12px;
        color: rgb(7, 17, 27);
      }
      .rank{
        line-height: 10px;
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
    }
    .overview-right{
      flex: 1;
      padding:6px 0 6px 24px;
      @media only screen and (max-width: 320px){
        padding-left: 6px;
      }
      .score-wrapper{
        margin-bottom: 8px;
        font-size: 0;
        .title{
          display: inline-block;
          vertical-align: top;
          line-height: 18px;
          font-size: 12px;
          color: rgb(7, 17, 27);
        }
        .star{
          display: inline-block;
          vertical-align: top;
          margin: 0 12px;
        }
        .score{
          display: inline-block;
          vertical-align: top;
          line-height: 18px;
          font-size: 12px;
          color: rgb(255, 153, 0);
        }
      }
      .delivery-wrapper{
        font-size: 0;
        .title{
          line-height: 18px;
          font-size: 12px;
          color: rgb(7, 17, 27);
        }
        .delivery{
          margin-left: 12px;
          font-size: 12px;
          color: rgb(147, 153, 159);
        }
      }
    }
  }

  .rating-wrapper{
    padding: 0 18px;
    .rating-item{
      display: flex;
      padding: 18px 0;
      @include border-1px(rgba(7,17,27,0.1));
      .avatar{
        flex: 0 0 28px;
        width: 28px;
        margin-right: 12px;
        img{
          border-radius: 50%;
        }
      }
      .content{
        flex: 1;
        position: relative;
        .name{
          margin-bottom: 4px;
          line-height: 12px;
          font-size: 10px;
          color: rgb(7, 17, 27);
        }
        .star-wrapper{
          margin-bottom: 6px;
          font-size: 0;
          .star{
            display: inline-block;
            vertical-align: top;
            margin-right: 6px;
          }
          .delivery{
            display: inline-block;
            vertical-align: top;
            line-height: 12px;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }
        .text{
          margin-bottom: 8px;
          line-height: 18px;
          color: rgb(7, 17, 27);
          font-size: 12px;
        }
        .recommend{
          line-height: 16px;
          font-size: 0;
          .icon-thumb_up, .item{
            display: inline-block;
            margin: 0 8px 4px 0;
            font-size: 9px;
          }
          .icon-thumb_up{
            color: rgb(0, 160, 220);
          }
          .item{
            padding: 0 6px;
            border: 1px solid rgba(7, 17, 27, 0.1);
            border-radius: 1px;
            color: rgb(147, 153, 159);
            background: #fff;
          }
        }
        .time{
          position: absolute;
          top: 0;
          right: 0;
          line-height: 12px;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
      }
    }
  }
</style>
